body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(1turn);
  }
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__hand {
  position: absolute;
  transform: translate(-50%);
}

.stopwatch__hand--minute {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 600s steps(60, end) infinite rotate;
  bottom: 50%;
  left: calc(50% - 1.5vmin);
  transform: rotate(0);
}

.stopwatch__hand--second {
  transform-origin: bottom;
  background-color: #2c8800;
  width: 1.5vmin;
  height: 38vmin;
  animation: 10s linear infinite rotate;
  bottom: 50%;
  left: calc(50% - .75vmin);
  transform: rotate(0);
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
